import requestHttps from '../../common/request.js'
export default {
	data() {
		return {
			serverImg:getApp().globalData.serverImg,
			navData:[
				{id:'0','mobile_name':'淘宝'},
				// {id:'0','mobile_name':'京东'},
				// {id:'1','mobile_name':'拼多多'},
				// {id:'2','mobile_name':'知了优选'},
				// {id:'3','mobile_name':'苏宁'}
			],
			current:0,
			navScrollLeft:0,
			keyword:'',
			windowWidth: 400,
			sercolse:true,
			twoTab:['精选','销量','最新','价格'],
			scut:0,
			prolodding:2,
			gohidden:true,    //返回顶部
			secdList:[],
			medix:true,
			page:1,
			flag:true,
			tabload:true,
			liloading:true,
			tsort:'new',
		}
	},
	methods: {
		// 切换
		swictNav:function(e){
			// console.log(e)
			var that=this
			var cur=e.currentTarget.dataset.curtab
			var id=e.currentTarget.dataset.id
			// navScrollLeft
			var singleNavWidth = that.windowWidth / 4
			    //tab选项居中                            
			that.navScrollLeft=(cur - 2) * singleNavWidth
			if(that.current==cur){
				return false
			}else{
				 that.current=cur
				 that.id=id
			} 
			// that.current=cur
		},
		// 销量
		twonav:function(e){
			console.log(e)
			var that=this
			var cur=e.currentTarget.dataset.sect
			
			if(cur==0){
				that.tsort='new'
			}else if(cur==1){
				that.tsort='sale_num_desc'
			}else if(cur==2){
				that.tsort='date_time'
			}else if(cur==3){
				that.tsort='price_asc'
			}else{
				
			}
			that.tabload=false
			that.secdList=[]
			that.flag=true
			that.scut=cur
			that.page=1
			that.getgoodList()
		},
		// 详情
		gopart:function(e){
			 var id=e.currentTarget.dataset.goods_id
			 uni.navigateTo({
				url: '../productdetail/productdetail?id='+id,
				success: res => {},
				fail: () => {},
				complete: () => {}
			 });
		},
		// 搜索文字
		bindinput:function(e){
			console.log(e)
			var that=this
			var val=e.detail.value
			 
			that.keyword=val
			console.log(that.keyword)
		},
		// 搜索框失去焦点
		bindblur:function(e){
			var that=this
			console.log(e)
			that.sercolse=true
		},
		// 聚焦时
		bindfocus:function(e){
			var that=this
			var secols = true
			console.log(e)
			var val = e.detail.value
			if (val.length > 0 || that.keyword != '' || that.keyword != undefined) {
			  secols = false
			}
			that.sercolse=secols  
		},
		// 确认
		bindconfirm:function(e){
			var that =this
			if(that.keyword){
				that.clicksear()
			}
		},
		colsekeywd:function(){
			var that=this
			that.keyword=''
			that.sercolse=true
			console.log(that.keyword)
			console.log('that.keyword')
		},
		// 返回上一页
		goback:function(){
			uni.navigateBack({
				delta: 1
			});
		},
		// 确定搜索
		clicksear:function(e){
			var that=this
			var keyword=that.keyword
			if(keyword){
				// uni.navigateTo({
				// 	url: '../searchlist/searchlist?keyword='+keyword,
				// 	success: res => {},
				// 	fail: () => {},
				// 	complete: () => {}
				// });
				that.page=1
			that.getgoodList()
			}
			
		},
		getgoodList:function(){
			var that=this 
			if(that.flag){
				if(that.keyword!=''|| that.keyword != 'undefined' || that.keyword!=undefined){
					requestHttps({
						url:'/tb/keyword/search',
						method:"POST",
						data:{
							kw:that.keyword,
							page:that.page,
							sort:that.tsort
						},
						success: res=>{
							console.log(res)
							var data=res.data
							var list=data.data
							var lood=2
							if(data.code==1){ 
								if(that.page==1){
									that.secdList=list
								}else{
									that.secdList=that.secdList.concat(list)
								}
								if (list != '') {
								      lood = 1
								  } else {
								      lood = 3
								  }
								  if(list==''&& that.page>1){
									  lood = 1
								  }
								  that.tabload=true
								 that.prolodding=lood
								if(list.length>=1){
									that.flag=true
									that.medix=true
									
									// that.prolodding=1
									that.liloading=true
								}else{
									that.liloading=true
									that.flag=false
									that.medix=false 
								}
							}
						}
					})
				}
			}
			
		},
		// 返回顶部
		gotop:function(e){
			uni.pageScrollTo({
				scrollTop:0,
				duration:300
			})
		}
	},
	onPageScroll(e) {
		// console.log(e)
		var that=this
		var scrollTop=e.scrollTop
		if(scrollTop<=300){
			that.gohidden=true
		}else{
			that.gohidden=false
		}
	},
	onReachBottom:function(){
		var that=this
		if(that.flag){
			var page=that.page
			page++
			that.page=page
			that.liloading=false
			that.getgoodList()
		}
	},
	onLoad:function(options){
		var that=this
		console.log(options)
		var kw=options.kw
		that.keyword=kw
		that.getgoodList()
	}
}
